.app{
    width: 400px;
    overflow-y: auto;
    padding: 22px;
    background-color: lightcoral;
    margin: auto;
    text-align: center;

}
.app img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
}
.app p{
    font-size: 24px;
    margin-left: 10px;
}
.app .text{
    display: flex;
}
 
.btn{
    display: flex;
    margin-top: 22px;
}
button{
    width: 140px;
    height: 40px;
    margin: auto;
    text-align: center;
    background-color: rgb(0, 165, 0);
    border: none;
    font-size: 22px;
    color: white;
    cursor: pointer;
}
.details{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 21px;
    margin-top: 22px;
}